%sharedTable2 {
    table-layout: fixed;
    border: $tableBorderGrey;
    border-collapse: separate;
    empty-cells: show;
    border-spacing: 0;
    display: flex;
    flex-flow: column nowrap;
    align-content: center;
    height: 30em;
    width: 82vw;
    overflow: auto;
    tr { 
        border: 1px solid #ccc;
        background-color: $trColorEven;
        padding-left: 0.5em;
    }
}


